@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;600;700&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;

.max-width {
  @apply lg:mx-auto lg:px-24 xl:px-48 px-5;
}
.bg-gradient {
  @apply bg-gradient-to-r from-sky-500 via-purple-500 to-pink-600;
}
.text-gradient {
  @apply bg-clip-text bg-gradient text-transparent;
}
.section {
  @apply pt-28 xl:pt-28 2xl:pt-44;
}
.text-content {
  @apply dark:text-light-content text-dark-content;
}
body {
  overflow-x: hidden;
}
/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
}

@keyframes pulseGlow {
  0%,
  100% {
    filter: drop-shadow(0 0 10px rgba(99, 102, 241, 0.6)); /* blue glow */
  }
  50% {
    filter: drop-shadow(0 0 15px rgba(147, 51, 234, 0.8)); /* purple glow */
  }
}

.animate-pulseGlow {
  animation: pulseGlow 2s infinite ease-in-out;
}
